.word-card{
    box-shadow: 3px 3px 8px rgba(0,0, 0, 0.3);
    border-radius: 10px;
    margin: 20px;
    padding: 10px;
    background-color: lightgoldenrodyellow;
    display: inline-block;
    width: 250px;
    height: 150px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor:pointer;
    user-select:none;
    position: relative;
}

.delete-card{
    font-weight: bold;
    font-size: 24px;
    border: 2px solid black;
    border-radius: 50%;
    position: absolute;
    right: 10px;
    top: 10px;
    display:inline-block;
    width: 25px;
    height: 25px;
    line-height: 25px;
    text-align: center;
}

.delete-card:hover{
    background-color:darkorange
}